<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <p>作者: {{blog.author}}</p>
    <p>分类:</p>
    <ul>
      <li v-for="category in blog.categories" :key="category">{{category}}</li>
    </ul>
    <article>{{blog.content}}</article>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      blog: {},
      id: this.$route.params.id
    };
  },
  created() {
    // 注意这里路径拼接
    this.$http
      .get("https://test-67e9c.firebaseio.com/posts/" + this.id + ".json")
      .then(data => {
        // console.log(data.json());
        return data.json();
      })
      .then(function(data) {
        console.log(data);
        this.blog = data;
      });
  }
};
</script>

<style scoped>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 2px dotted #aaa;
}
</style>


