<template>
  <div id="show-blogs" v-theme:colomn="'narrow'">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索" />
    <div class="single-blog" v-for="blog in filterBlogs" :key="blog.title">
      <!-- 自定义指令v-rainbow 传的参数可以是字符串 数组 或者对象 -->
      <router-link v-bind:to="'/blog/' + blog.id">
        <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
      </router-link>
      <!-- 注意这里循环的内容要与对象的属性一致 -->
      <article>{{blog.content | snippet}}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    // ../../static/posts.json 请求本地数据
    // http://jsonplaceholder.typicode.com/posts 请求测试数据的接口
    // https://test-67e9c.firebaseio.com/posts.json  这是firebase数据库接口
    this.$http
      .get("https://test-67e9c.firebaseio.com/posts.json")
      .then(data => {
        // console.log(data);
        // console.log(data.json()); // 注意用firebase请求回来body的是一个对象，对象不能用slice方法
        // return data.json();
        // console.log(data.body);
        var obj = data.body;
        var blogsArr = [];
        for(let key in obj) {
          // console.log(obj[key]);
          obj[key].id = key; // 把每个唯一标志当做id存储进数组
          blogsArr.push(obj[key]);
        }
        // console.log(blogsArr)
        this.blogs = blogsArr;
      })
      // .then(function (data) {
      //   let blogsArr = [];
      //   for(let key in data) {
      //     console.log(key);
      //   }
      // })
  },

  computed: {
    // 实现搜索博客
    filterBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },

  // 自定义过滤器 把标题改大写，内容不全部显示
  filters: {
    toUppercase(value) {
      return value.toUpperCase();
    },
    snippet(value) {
      return value.slice(0, 100) + "...";
    }
  },

  // 组件内自定义指令
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    },
    theme: {
      bind(el, binding, vnode) {
        if (binding.value == "wide") {
          el.style.maxWidth = "1260px";
        } else if (binding.value == "narrow") {
          el.style.maxWidth = "560px";
        }
        if (binding.arg == "colomn") {
          el.style.background = "#67c";
          el.style.padding = "50px";
        }
      }
    }
  }
};
</script>

<style scoped>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  background: #ccc;
}
.single-blog a {
  text-decoration: none;
  color: #444;
}
input[type="text"] {
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
}
</style>



